.home {
  /* max-width: 1250px;
  margin-left: auto;
  margin-right: auto; */
  max-width: 100%;
}
.home__image {
  width: 100%;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  z-index: -1;
  margin-bottom: -150px;
}
.home__row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
}
.home__row_img {
  margin: auto;
  cursor: pointer;
  max-width: 100%;
}
@media (max-width: 600px) {
  .home__row {
    flex-wrap: wrap;
    align-content: center;
  }
  .home__row_img:hover {
    transform: scale(1) !important;
  }
}
