.login {
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login__logo {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 100px;
  object-fit: contain;
  padding: 5px;
}
.logo__container {
  width: 300px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid lightgray;
}
.logo__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}
.logo__container > form > h5 {
  margin-bottom: 5px;
}
.logo__container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
}
.logo__container > p {
  margin-top: 15px;
  font-size: 12px;
}
.login__signinButton {
  background-color: #f0c14b;
  width: 100%;
  border-radius: 2px;
  border: 1px solid;
  height: 30px;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
}
.login__registerButton {
  width: 100%;
  border-radius: 2px;
  border: 1px solid;
  height: 30px;
  margin-top: 10px;
  border-color: darkgray;
}
